nav{
  background-color: rgb(239, 239, 239);
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  align-items: center;
  top: 0;
  right: 0;
  left: 0;
  padding: 0 20px;
  font-family: 'PT Sans';
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.15);
  
}



.containerText {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 602px;
  text-align: none;
  text-decoration: none;
  font-family: 'Raleway', sans-serif;
  
}

ul li{
  display: inline-block;
}

nav .btn{
  margin-left: 300px;
  margin-top: 2px;
  font-size: 15px;
  font-weight: bold;
  background-color: #1E90FF;
  width: 130px;
  padding: 13px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  -webkit-transition-duration: 0.3s ;
  transition-duration: 0.3s;
  -webkit-transition-property: box-shadow, transoform;
  transition-property: box-shadow, transform;
  font-family: 'Raleway', sans-serif;
}

.btn:hover, .btn:focus, .btn:active{
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  -webkit-transition-delay: scale(1.1);
  transform: scale(1.1);
}








/* Section Titles */
.section--title {
  color: var(--heading-color);
  font-size: 21px;
  text-align: center;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  display: flex;
  align-items: flex-start;
  margin-left: 10px;
  margin-bottom: -50px;
  font-family: 'PT Sans', sans-serif;
  
}





/* Hero Section CSS */
.hero--section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 133.333px 85.333px 133.333px;
  align-items: center;
  justify-content: space-between;
  gap: 32px;
  background-color: var(--bg-shade);
  font-family: 'PT Sans', sans-serif; 
}


.hero--section--title {
  color: var(--heading-color);
  font-size: 74.667px;
  font-weight: 700;
  line-height: 90px;
  align-self: stretch;
}
.hero--section--title--color {
  color: var(--primary);
  
}
.hero--section-description {
  color: var(--darkblue);
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
}
.hero--section--img {
  display: flex;
} 
.hero--section--img > img {
  width: 50%;
  height: 50%;
  padding-left: 20%;
  border-radius: 10px;
}

  /* HERO CSS END */


